@import url(https://fonts.googleapis.com/css2?family=Nunito:wght@200;300;400;500;600;700&display=swap);
.Container{max-width:1170px;margin:0 auto}

.Wrapper{width:100%;margin:0 auto;padding:0 15px;box-sizing:border-box;border:1px solid transparent}@media (min-width: 768px){.Wrapper{padding:0 32px}}@media (min-width: 1024px){.Wrapper{padding:0 60px}}@media (min-width: 1170px){.Wrapper{padding:0}}

.Button{display:inline-block;height:34px;width:100px;font-size:var(--fs-md);border-radius:var(--button-br);color:var(--button-color);background-color:var(--button-primary-bg);cursor:pointer}.Button__container{display:flex;align-items:center;justify-content:center;width:100%;height:100%;-webkit-touch-callout:none;-webkit-user-select:none;-ms-user-select:none;user-select:none}.Button:hover{background-color:var(--button-hover-bg)}.Button--disabled,.Button--disabled:hover{color:var(--button-disabled-color);background-color:var(--button-disabled-bg)}

.Header{background-color:var(--white);position:fixed;top:0;right:0;width:100%;z-index:9999}.Header__container{min-width:328px;height:60px;display:flex;flex-direction:row;justify-content:space-between;align-items:center;flex-wrap:nowrap}.Header__button{margin-left:5px}

.Promo__poster{width:100%;min-width:360px;height:100vh;box-sizing:border-box;background-repeat:no-repeat;background-size:cover;background-position:center;color:var(--white);background-image:url(../../static/media/promo-360.f09745ec.png);font-weight:300;text-align:center;display:flex;align-items:center}.Promo h1{margin:0 auto;font-size:var(--fs-h1);font-weight:var(--fw-normal);line-height:var(--lh-h1);max-width:350px;text-align:center}.Promo p{margin:0 auto;padding-top:21px;max-width:328px;font-size:var(--fs-md);font-weight:var(--fw-normal);line-height:var(--lh-md);padding-bottom:32px}@media (min-width: 768px){.Promo__poster{background-image:url(../../static/media/promo-768.78baa64c.png)}.Promo h1{max-width:450px}.Promo p{max-width:381px;padding-bottom:36px}}@media (min-width: 1024px){.Promo__poster{background-image:url(../../static/media/promo-1024.5fff95b2.png)}}


/* Document
 * ========================================================================== */

/**
 * 1. Correct the line height in all browsers.
 * 2. Prevent adjustments of font size after orientation changes in
 *    IE on Windows Phone and in iOS.
 */

html {
  line-height: 1.15; /* 1 */
  -ms-text-size-adjust: 100%; /* 2 */
  -webkit-text-size-adjust: 100%; /* 2 */
}

/* Sections
 * ========================================================================== */

/**
 * Correct the font size and margin on `h1` elements within `section` and
 * `article` contexts in Chrome, Edge, Firefox, and Safari.
 */

h1 {
  font-size: 2em;
  margin: 0.67em 0;
}

/* Grouping content
 * ========================================================================== */

/**
 * Remove the margin on nested lists in Chrome, Edge, IE, and Safari.
 */

dl dl,
dl ol,
dl ul,
ol dl,
ul dl {
  margin: 0;
}

/**
 * Remove the margin on nested lists in Edge 18- and IE.
 */

ol ol,
ol ul,
ul ol,
ul ul {
  margin: 0;
}

/**
 * 1. Add the correct box sizing in Firefox.
 * 2. Show the overflow in Edge 18- and IE.
 */

hr {
  box-sizing: content-box; /* 1 */
  height: 0; /* 1 */
  overflow: visible; /* 2 */
}

/**
 * Add the correct display in IE.
 */

main {
  display: block;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

pre {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/* Text-level semantics
 * ========================================================================== */

/**
 * Add the correct text decoration in Edge 18-, IE, and Safari.
 */

abbr[title] {
  text-decoration: underline;
  text-decoration: underline dotted;
}

/**
 * Add the correct font weight in Chrome, Edge, and Safari.
 */

b,
strong {
  font-weight: bolder;
}

/**
 * 1. Correct the inheritance and scaling of font size in all browsers.
 * 2. Correct the odd `em` font sizing in all browsers.
 */

code,
kbd,
samp {
  font-family: monospace, monospace; /* 1 */
  font-size: 1em; /* 2 */
}

/**
 * Add the correct font size in all browsers.
 */

small {
  font-size: 80%;
}

/* Embedded content
 * ========================================================================== */

/**
 * Hide the overflow in IE.
 */

svg:not(:root) {
  overflow: hidden;
}

/* Forms
 * ========================================================================== */

/**
 * Remove the margin on controls in Safari.
 */

button,
input,
select {
  margin: 0;
}

/**
 * 1. Show the overflow in IE.
 * 2. Remove the inheritance of text transform in Edge 18-, Firefox, and IE.
 */

button {
  overflow: visible; /* 1 */
  text-transform: none; /* 2 */
}

/**
 * Correct the inability to style buttons in iOS and Safari.
 */

button,
[type="button"],
[type="reset"],
[type="submit"] {
  -webkit-appearance: button;
}

/**
 * Correct the padding in Firefox.
 */

fieldset {
  padding: 0.35em 0.75em 0.625em;
}

/**
 * Show the overflow in Edge 18- and IE.
 */

input {
  overflow: visible;
}

/**
 * 1. Correct the text wrapping in Edge 18- and IE.
 * 2. Correct the color inheritance from `fieldset` elements in IE.
 */

legend {
  box-sizing: border-box; /* 1 */
  color: inherit; /* 2 */
  display: table; /* 1 */
  max-width: 100%; /* 1 */
  white-space: normal; /* 1 */
}

/**
 * 1. Add the correct display in Edge 18- and IE.
 * 2. Add the correct vertical alignment in Chrome, Edge, and Firefox.
 */

progress {
  display: inline-block; /* 1 */
  vertical-align: baseline; /* 2 */
}

/**
 * Remove the inheritance of text transform in Firefox.
 */

select {
  text-transform: none;
}

/**
 * 1. Remove the margin in Firefox and Safari.
 * 2. Remove the default vertical scrollbar in IE.
 */

textarea {
  margin: 0; /* 1 */
  overflow: auto; /* 2 */
}

/**
 * 1. Correct the odd appearance in Chrome, Edge, and Safari.
 * 2. Correct the outline style in Safari.
 */

[type="search"] {
  -webkit-appearance: textfield; /* 1 */
  outline-offset: -2px; /* 2 */
}

/**
 * Correct the cursor style of increment and decrement buttons in Safari.
 */

::-webkit-inner-spin-button,
::-webkit-outer-spin-button {
  height: auto;
}

/**
 * Correct the text style of placeholders in Chrome, Edge, and Safari.
 */

::-webkit-input-placeholder {
  color: inherit;
  opacity: 0.54;
}

/**
 * Remove the inner padding in Chrome, Edge, and Safari on macOS.
 */

::-webkit-search-decoration {
  -webkit-appearance: none;
}

/**
 * 1. Correct the inability to style upload buttons in iOS and Safari.
 * 2. Change font properties to `inherit` in Safari.
 */

::-webkit-file-upload-button {
  -webkit-appearance: button; /* 1 */
  font: inherit; /* 2 */
}

/**
 * Remove the inner border and padding of focus outlines in Firefox.
 */

::-moz-focus-inner {
  border-style: none;
  padding: 0;
}

/**
 * Restore the focus outline styles unset by the previous rule in Firefox.
 */

:-moz-focusring {
  outline: 1px dotted ButtonText;
}

/**
 * Remove the additional :invalid styles in Firefox.
 */

:-moz-ui-invalid {
  box-shadow: none;
}

/* Interactive
 * ========================================================================== */

/*
 * Add the correct display in Edge 18- and IE.
 */

details {
  display: block;
}

/*
 * Add the correct styles in Edge 18-, IE, and Safari.
 */

dialog {
  background-color: white;
  border: solid;
  color: black;
  display: block;
  height: -moz-fit-content;
  height: -webkit-fit-content;
  height: fit-content;
  left: 0;
  margin: auto;
  padding: 1em;
  position: absolute;
  right: 0;
  width: -moz-fit-content;
  width: -webkit-fit-content;
  width: fit-content;
}

dialog:not([open]) {
  display: none;
}

/*
 * Add the correct display in all browsers.
 */

summary {
  display: list-item;
}

/* Scripting
 * ========================================================================== */

/**
 * Add the correct display in IE.
 */

template {
  display: none;
}

/* User interaction
 * ========================================================================== */

:root {
  /* Typography */
  --family: 'Nunito', sans-serif;
  --fs-sm: 12px;
  --fs-md: 16px;
  --fs-h1: 40px;

  --fw-normal: 400;
  --lh-sm: 14px;
  --lh-md: 26px;
  --lh-h1: 40px;

  /* Other */
  --yellow: #F4E041;
  --blue: #00BDD3;
  --light-grey: #F8F8F8;
  --bg-color: #E5E5E5;
  --white: #FFFFFF;
  --black: rgba(0, 0, 0, 0.87);
  
  /* UI */
  --button-color: rgba(0, 0, 0, 0.87);
  --button-primary-bg: #F4E041;
  --button-hover-bg: #FFE302;
  --button-disabled-bg: #B4B4B4;
  --button-disabled-color: rgba(255, 255, 255, 0.87);
  --button-br: 80px;
  --button-radii: 4px;
  
  --field-border-enabled: #D0CFCF;
  --field-radii: 4px;
  --field-helper-text: #7E7E7E;
  --field-border-error: rgba(203, 61, 64, 1);
  --error-color: #CB3D40;

  --card-br: 10px;
}

body {
  background-color: #E5E5E5;
  background-color: var(--bg-color);
  /* min-width: 360px; */
}

* {
  box-sizing: border-box;
  font-family: 'Nunito', sans-serif;
  font-family: var(--family);
  font-size: 16px;
  font-size: var(--fs-md);
  margin: 0;
  padding: 0;
}

